import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/routerPage/index'
import about from '@/components/routerPage/about'
import demo from '@/components/routerPage/demo'
import sign from '@/components/routerPage/sign'


Vue.use(Router)

export default new Router({
//	mode:'history',
	base:'/dist',
  routes: [
     {
     	path:'/',
     	name:index,
     	component:index,
     },
     {
     	path:'/about',
     	name:about,
     	component:about,
     },
     {
     	path:'/demo',
     	name:demo,
     	component:demo,
     },
     {
     	path:'/sign',
     	name:sign,
     	component:sign,
     }
//  {
//	path:'*',
//	redirect:'/'
//  },
   
  
  //如果选择的路由上面都没有，
//

  ]
})



